.videoCard {
    color: wheat;
    width: 500px;
    height: 400px;
    padding: 20px;
    transition: tramsform 100ms;
}

.videoCard:hover {
    transform: scale(1.09);
}

.videoCard > img {
    height: 300px;
    width: 500px;
    object-fit: contain;
}

.videoCard > h2 {
    padding-top: 10px;
}

.videoCard_stats {
    display: none;
}

.videoCard:hover > .videoCard_stats {
    display: inline;
}
